<template>
  <div class="song">
    <div class="bgmask"></div>
    <div class="bg" :style="{'background-image': 'url('+cover+')','background-color': 'rgb(255,255,255)'}"></div>
    <div class="music-contianer">

    </div>
  </div>
</template>
<script>
import { req_lyric, req_song_url } from "../network/song/index.js";
import tools from "../tools/formatData.js";
import { req_search_singer } from "../network/search/index.js";
export default {
  name: "song",
  components: {},
  data() {
    return {
      id: this.$route.query.id,
      url: "",
      name: this.$route.query.songName,
      cover: "",
      // 歌词处理后
      parseLyric: [],
    };
  },
  methods: {
    // 获取音频
    getsongUrl() {
      req_song_url(this, { id: this.id })
        .then(res => {
          this.url = res.data[0].url;
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取歌词
    getlyric() {
      req_lyric(this, { id: this.id })
        .then(res => {
          this.parseLyric = tools._parseLyric(res.lrc.lyric);
        })
        .catch(error => {
          console.log(error);
        });
    },
    searchSong() {
      req_search_singer(this, {
        keywords: this.name,
      })
        .then(res => {
          this.cover = res.result.songs[0].al.picUrl;
        })
        .catch(error => {
          console.log(error);
        });
    },
  },
  created() {
    this.getsongUrl();
    this.getlyric();
    this.searchSong();
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
.song {
  position: relative;
  z-index: 3;
  // 用于测试
  width: 100vw;
  height: 100vh;
  
  .bg {
    position: absolute;
    @include bgmask();
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;
  }
  .bgmask {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.35);
    z-index: -2;
  }
  .music-container {

    z-index: 3;
  }
}
</style>